<svelte:head>
    <link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootswatch/4.5.0/flatly/bootstrap.min.css">
    <link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css">
</svelte:head>
<style>
</style>
<script>
  import {Row, Col, Container, Jumbotron, Label, Input, Button, Navbar, NavbarBrand, Nav, NavItem} from 'sveltestrap'
  import { Router, Link, Route } from "svelte-routing";

  import Footer from './Footer.svelte';

  import Konva from 'konva';
  import { onMount } from 'svelte';
  import { beforeUpdate, afterUpdate } from 'svelte';
  import { auth, googleProvider, twitterProvider, db, firebaseAnalytics } from './firebase';
  let user = auth.currentUser;

  let user_text, website_name;
  let stage,width,height;
  let layer = new Konva.Layer();
  let Shapelayer =  new Konva.Layer();
  let isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
  
//code to check if user downloaded and then increment.
let timesave, contentMadeSoFar=0;

function statsCounter(){
  db.collection('usersDB').doc(user.uid).get().then(doc => {
      if (doc.exists) {
          //alert("stat counter if statement me aya hu")
          //if user is already there, then add +1
              let val = contentMadeSoFar+1
              db.collection('usersDB').doc(user.uid).set({
                      totalCreated: val
              })
              contentMadeSoFar = val
              timesaved()
      } else {
         // alert("stat counter else statement me aya hu")
          db.collection('usersDB').doc(user.uid).set({
                      totalCreated: val
              })
              contentMadeSoFar = val
              timesaved()
      }
      }).catch(function(error) {
          console.log("Error getting document:", error);
      });
}

function generateShapes() {
		let x;
		for (x =0; x<5; x++){

		if (x%2 == 0){
			Shapelayer.add(
			new Konva.Rect({
			  x: Math.random() * stage.width(),
			  y: Math.random() * stage.height(),
			  width: 50 + Math.random() * 50,
			  height: 50 + Math.random() * 50,
			  fill: Konva.Util.getRandomColor(),
			  rotation: Math.random() * 360,
			  draggable: true,
			  name: 'object'
			})
		  );}
		  else {
			Shapelayer.add(new Konva.Circle({
			x: Math.random() * stage.width(),
			y: Math.random() * stage.height(),
			radius: 70,
			  width: 50 + Math.random() * 50,
			  height: 50 + Math.random() * 50,
			fill: Konva.Util.getRandomColor(),
			stroke: Konva.Util.getRandomColor(),
			strokeWidth: 1,
			  rotation: Math.random() * 360,
			  draggable: true,
			  name: 'object'
			}));
		  }
			stage.add(Shapelayer);
			Shapelayer.draw();
		}
	}

function userData() {
      user_text = document.getElementById('textContent').value;
      website_name = document.getElementById('websiteName').value;
      
    let ut = new Konva.Text({
        x: 50,
        y: 135,
        text: user_text,
        fontSize: "30",
        fontFamily: "Verdana",
        fill: document.getElementById("userTextColor").value
      }); 

      ut.width(600);
      

      let wn = new Konva.Text({
        x: 550,
        y: 325,
        text: website_name,
        fontSize: "15",
        fontFamily: "Verdana",
        fill: "white"
	  });

      layer.add(ut);
      layer.add(wn);
      layer.moveToTop();
      
      // to check if they want random shapes or not
      if (document.querySelector('#randomShapes:checked') !== null)
		{
            generateShapes();
        }
        else {
            console.log("shapes unselected");
        }

	  stage.add(layer);
  }

function makeNshow(e) {
    e.preventDefault();
        var cnvs = document.getElementById("ourCanvas");
        if (cnvs.style.display == "none") {
            cnvs.style.display = "block";
        } else {
            cnvs.style.display = "none";
        }

        userData();
    }

function resetDesign(){
    stage.destroyChildren();
    layer = new Konva.Layer();
    let bgRect = new Konva.Rect({
					x:0,y:0,
					width: width,
					height: height,
					fill: '#'+(0x1000000+(Math.random())*0xffffff).toString(16).substr(1,6),
					draggable: false,
		});
    layer.add(bgRect)
    layer.moveToBottom()
    //stage.add(bglayer)
    stage.add(layer);
    layer.draw()
}

function forTwitter() {
		if (isMobile) {
            var dataURL = stage.toDataURL({  pixelRatio: 2 });
			downloadURI(dataURL, 'posterTW.png');
        }
        else {
            var dataURL = stage.toDataURL({  pixelRatio: 1 });
			downloadURI(dataURL, 'posterTW.png');
        }
        statsCounter();

}

function forFacebook() {
		if (isMobile) {
            var dataURL = stage.toDataURL({  pixelRatio: 4 });
			downloadURI(dataURL, 'posterFB.png');
        }
        else {
            var dataURL = stage.toDataURL({  pixelRatio: 2 });
			downloadURI(dataURL, 'posterFB.png');
        }
        statsCounter();

}

function forLinkedIn() {
    if (isMobile) {
            var dataURL = stage.toDataURL({  pixelRatio: 4 });
			downloadURI(dataURL, 'posterLN.png');
        }
        else {
            var dataURL = stage.toDataURL({  pixelRatio: 2 });
			downloadURI(dataURL, 'posterLN.png');
        }
        statsCounter();
}

function downloadURI(uri, name) {
        let link = document.createElement('a');
        link.download = name;
        link.href = uri;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      }

    beforeUpdate(() => {
	console.log("beforeUpdate was called");
		//setting default values to canvas dimensions
		width = 720;
		height = 350;
		// then create default one layer
		//layer = new Konva.Layer();
		
	});

    onMount(() => {

        // code to intialize counter for stats
        db.collection('usersDB').doc(user.uid).get().then(doc => {
            if (doc.exists) {
                let userData = doc.data();
                contentMadeSoFar = userData.totalCreated;
            } else {
                db.collection('usersDB').doc(uid).set({
                            totalCreated: 0
                })
            }
            }).catch(function(error) {
                console.log("Error getting document:", error);
            });

        // first we need to create a stage
		stage = new Konva.Stage({
			container: 'konva-stage',   // id of container <div>
			width: width,
			height: height
		});

        let bgRect = new Konva.Rect({
					x:0,y:0,
					width: width,
					height: height,
					fill: '#'+(0x1000000+(Math.random())*0xffffff).toString(16).substr(1,6),
					draggable: false,
		});

        var layer = new Konva.Layer();
			
			layer.add(bgRect)
			layer.moveToBottom()
			//stage.add(bglayer)
            stage.add(layer);
			layer.draw()
    });
</script>
<div>
    <Container fluid>
    <!--{#if user} UNCOMMENT WHEN YOU DONE WITH THIS--> 
	<Row class="pt-5">
		<Col sm="12" class="p-3">
			<h1 class="text-center">Quick Mode</h1>
		</Col>
        <Col sm="12" class="text-center">
            <h4>Enter the information below to quickly generate your content.</h4>
		</Col>
    </Row>

    <form action="#" on:submit={makeNshow}>
        <Row class="mt-4 justify-content-center">
            <Col lg="6">
                <Label class="font-weight-bold" for="inputText">Enter Text:-</Label>
                <Input type="textarea" placeholder="Marketing is easier with Designtack" id="textContent" required/>
            </Col>
        </Row>

        <Row class="mt-4 justify-content-center">
            <Col lg="6">
                <Label class="font-weight-bold" for="inputColor">Select Font Color:-</Label>
                <Input
                        type="color"
                        name="color"
                        id="userTextColor"
                        value="#ffffff"
                        placeholder="colorPicker" />
             </Col>
        </Row>

        <Row class="mt-4 justify-content-center">
            <Col lg="6">
                <Label class="font-weight-bold" for="website">Enter Your Website Name:-  </Label><span>(optional)</span>
                <Input type="text" id="websiteName" name="website" placeholder="www.yoursite.com"/>
            </Col>
        </Row>
        <Row class="mt-4 justify-content-center">
            <Col lg="5">
                <Input type="checkbox" id="randomShapes"/><label for='randomShapes' class="font-weight-bold">Add random shapes?</label>
            </Col>
        </Row>
        <Row class="mt-2 mx-4 text-center">
            <Col>
                <Button class="m-1" block color="primary" type="submit">Generate My Content</Button>
            </Col>
            <Col>
                <Button class="m-1" block color="danger" on:click={resetDesign}>Reset</Button>
            </Col>
        </Row>
    </form>
        <Row class="mt-2 justify-content-center">
        <div id="ourCanvas" style="display: none">
            <Row class="pt-5">
                <Col sm="12" class="text-center">
                    <h4 class="text-info">Your generated content is ready!</h4>
                </Col>
            </Row>
                <div id="stage-parent">
                        <div id="konva-stage"></div>
                </div>
                <div>
                    <Row class="pt-5">
                        <Col sm="12" class="text-center">
                            <h4>Download For Different Social Media.</h4>
                        </Col>
                    </Row>
                    <Row class="mt-2 mx-4 text-center">
                    <Col>
                    <Button block color="primary" on:click={forFacebook}>Download for Facebook</Button>
                    </Col>
                    <Col>
                    <Button block color="primary" on:click={forTwitter}>Download for Twitter</Button>
                    </Col>
                    <Col>
                    <Button block color="primary" on:click={forLinkedIn}>Download for Linkedin</Button>
                    </Col>
                </Row>
                </div>
        </div>
        </Row>
        <hr/>
<Footer />
