<Row class="pt-5">
    <Col sm="12" class="p-3">
        <h1 class="text-center">Oops!</h1>
    </Col>
    <Col sm="12" class="text-center">
        <h4>You seem to be logged out.</h4>
        <h4><a href="/Login" use:link>Try logging in again?</a>.</h4>
    </Col>
</Row>
<Footer />
{/if}-->
</Container>
</div>